.top {
  padding: 20px 0 20px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 99;
  background-image: linear-gradient(
    rgba(255, 255, 255, 1),
    rgba(255, 255, 255, 0.9)
  );
}

.top .inner {
  display: flex;
  flex-direction: row;
}

.inner img {
  width: 160px;
}

.inner {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

.topMid {
  flex-grow: 1;
  line-height: 24px;
  padding-left: 80px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-weight: 500;
}

.topMid span {
  margin-right: 40px;
  cursor: pointer;
  min-width: 140px;
  font-weight: 500;
  text-align: center;
  transition: all linear 100ms;
  color: #444;
}
.topMid span:hover {
  font-weight: 600;
  color: #000;
}

.topMid span.active {
  font-weight: bold;
  color: #fff;
}

.topRight {
  font-size: 14px;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  flex-shrink: 0;
  font-weight: 500;
  align-items: center;
  width: 138px;
}

.topRight span {
  margin-left: 10px;
}

@media only screen and (max-width: 600px) {
  .inner {
    width: 98%;
  }
  .topMid {
    padding: 5px;
  }
  .topMid span {
    width: auto;
    min-width: 0;
    margin-right: 10px;
    font-size: 12px;
  }
  .topRight {
    width: auto;
    font-size: 12px;
  }
  .topMid span:hover {
    font-weight: 500;
    color: #000;
  }
  .nav {
    margin: 10px 20px;
  }
  .inner img {
    opacity: 0;
    width: 40px;
  }
}
