.color {
  color: orange;
}

.wrap {
  position: relative;
}

.nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: 40px;
}
.content {
  max-width: 1200px;
  margin: 0 auto;
  margin-top: 80px;
  padding: 1px;
}

.navIcon {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 36px;
  border: 1px solid #999;
  border-radius: 18px;
  font-size: 12px;
  padding: 0 3px;
  color: #666;
  padding-right: 8px;
  cursor: pointer;
  font-weight: 500;
  margin: 0 10px;
  transform: all linear 200ms;
}
.navIcon:hover {
  background-color: #f9f9f9;
  color: #000;
  font-weight: 500;
}

.navIcon img {
  width: 30px;
  margin-right: 5px;
}

.itemWrap {
}

.coinTitle {
  padding-top: 60px;
  text-align: center;
  font-weight: 500;
  font-size: 32px;
}

.item {
  width: 30%;
  display: inline-block;
  margin-bottom: 50px;
  cursor: pointer;
  margin: 1.5%;
}

.item img {
  background-color: #f8f8f8;
  max-width: 100%;
  width: 100%;
}

.itemInfo {
  font-weight: 500;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 10px;
}

.itemName {
  color: #333;
  font-weight: 500;
  margin-right: 30px;
}

.itemPrice {
  font-weight: bold;
}

@media only screen and (max-width: 600px) {
  .item {
    width: 80%;
    margin: 0;
    margin-bottom: 40px;
  }

  .itemWrap {
    text-align: center;
  }
  .nav {
    flex-wrap: wrap;
    margin: 10px;
  }
  .navIcon {
    margin-bottom: 20px;
  }
}
