@import "@share/style/s.module.scss";

.wrap :global .ant-input-number-handler-wrap {
  display: none;
}

.wrap input {
  text-align: center;
  font-size: 18px;
}

.wrap :global .ant-input-number {
  border: none;
  font-weight: 500;
  box-shadow: none !important;
}

.wrap :global input:focus {
  iutline: none;
}

.wrap :global .ant-input-number:hover {
  border: none !important;
  box-shadow: none !important;
}

.add,
.minus {
  font-weight: 500;
  font-size: 20px;
  cursor: pointer;
  color: #666;
  transition: all linear 200ms;
}

.add:hover,
.minus:hover {
  color: #000;
}
