@import "@share/style/s.module.scss";

.wrap {
  background: #fff;
}

:global .ant-drawer-body {
  padding: 5px;
}

.content {
  max-width: 1200px;
  margin: 0 auto;
  margin-top: 100px;
  padding: 1px;
  width: 90%;
}

.item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.left {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
}

.right {
  flex-grow: 1;
  padding-left: 60px;
}

.left .imgWrap {
  background: $itemBackground;
  width: 100%;
  padding: 20px;
}

.left img {
  width: 100%;
}

.keyWrap {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 96%;
}

.keyItem {
  width: 50%;
  text-align: center;
  padding-bottom: 20px;
  padding-top: 20px;
}

.keyItem:first-child {
  border-right: 1px solid #f1f1f1;
}

.keyWrap {
  border-bottom: 1px solid #f1f1f1;
}

.keyWrap:last-child {
  border-bottom: 0;
}

.keyWrap .value {
  text-transform: uppercase;
  font-weight: 500;
  font-size: 21px;
}

.keyWrap .label {
  font-size: 14px;
}

.right h2 {
  font-size: 28px;
}

.right .price {
  font-size: 18px;
  font-weight: 500;
  line-height: 40px;
}

.right .label {
  font-size: 16px;
  font-weight: 500;
  margin-top: 80px;
}

.infoWrap {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 40px;
}

.infoItem {
  max-width: 250px;
  width: 30%;
}
.infoLabel {
  font-size: 14px;
}
.infoValue {
  font-size: 20px;
  font-weight: 500;
}

.quantityInput {
  margin-top: 20px;
}

.paymentMethod {
  width: 260px;
  margin: 30px 0;
}

.drawerHeader {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  padding: 20px 10px;
}

.drawerHeaderHolder {
  display: inline-block;
  width: 40px;
}

.drawerHeader p {
  flex-grow: 1;
  text-align: center;
  font-size: 16px;
  font-weight: 500;
}

.amountWrap {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.amountLabel {
  font-size: 16px;
  font-weight: 500;
  color: #333;
}

.amountValue {
  font-size: 30px;
  color: $mainBlue;
  font-weight: 500;
  margin-top: 20px;
}

.qrCode {
  width: 300px;
  min-height: 300px;
  margin: 30px auto;
}

.qrWrap {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.qrTips {
  font-size: 12px;
  color: #444;
  text-align: center;
  line-height: 18px;
}

.buttonWrap {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
}

.prdInfo {
  margin-top: 30px;
}
.prdInfoRow {
  display: flex;
  width: 100%;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: 12px;
  font-weight: 500;
  line-height: 24px;
  padding: 0 20px;
}
.prdInfoRow:first-child {
  border-bottom: 1px solid #eee;
}

.priceWrap {
  display: flex;
  flex-direction: row;
  padding-bottom: 30px;
}
.priceItem {
  width: 50%;
  max-width: 300px;
}

.totalPrice {
  font-weight: 500;
  font-size: 18px;
  margin-top: 20px;
}

@media only screen and (max-width: 1500px) {
  .left {
    width: 40%;
  }
  .content {
    max-width: 1000px;
  }
  .infoWrap {
    margin-top: 20px;
  }
  .priceWrap {
    padding-bottom: 20px;
  }
  .drawerHeader {
    padding: 10px;
  }
  .qrCode {
    width: 230px;
  }
  .amountWrap {
    margin-top: 20px;
  }
  .amountValue {
    margin-top: 10px;
  }
  .qrCode {
    margin: 10px auto;
    margin-bottom: 0;
  }
}

@media only screen and (max-width: 600px) {
  .content {
    width: 96%;
  }
  .item {
    flex-direction: column-reverse;
  }
  .left {
    margin-top: 20px;
    padding-top: 20px;
    border-top: 1px solid #eee;
    width: 100%;
    padding-bottom: 20px;
  }
  .right {
    padding: 0;
    width: 100%;
    text-align: center;
  }
  .keyWrap .value {
    font-size: 18px;
  }
  .infoLabel {
    font-size: 12px;
  }
  .infoValue {
    font-size: 14px;
  }
  .infoItem {
    width: 33%;
  }
  .payMethod {
    text-align: center;
  }
  .payMethod img {
    margin: 10px auto;
  }
}
